body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #2d2d2d;
  background: -webkit-linear-gradient(303deg, #2d2d2d 0%, #2c3e50 74%);
  background: linear-gradient(147deg, #2d2d2d 0%, #2c3e50 74%);
  background-repeat: no-repeat;
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'Cocogoose Pro Inline';
  font-style: normal;
  font-weight: normal;
  src: local('Cocogoose Pro Inline'), url("/robofriends/static/media/Cocogoose Pro Inline Trial.22c5adf6.woff") format('woff');
  }

h1 {
  font-family: 'Cocogoose Pro Inline';
  font-weight: 100;
  color: #07928d;
}

